.footer{
    margin: 0 auto;
    padding: 50px;
    text-align: center;
    &__titel{
        padding: 30px 0;
        font-size: 34px;
        font-weight: 800;
    }
    &__losung{
        padding: 20px;
        font-size: 16px;
        font-weight: 400;
    }
    &__social{
        padding: 20px;
        font-size: 60px;
    }
    &__social i{
        padding: 0 25px;
    }
    &__location{
        padding: 10px;
        font-size: 12px;
        font-weight: 400;
    }
}